import axios from "axios"
import { useEffect, useState } from "react"

const CreateItem =()=>{
    const [description,setDescription]=useState("")
    const [id,setId]=useState("")
    const [type,setType]=useState("")
    const [data,setData]=useState([])
    const [price,setPrice]=useState("")
    const createItem=async(e)=>{
        e.preventDefault()
        // const itemData = new FormData()
        // itemData.append("description",description)
        // itemData.append("id",id)
        // itemData.append("type",type)
        // itemData.append("price",price)

        const {data}=await axios({
            method: "POST",
            url:"/api/v1/create-item",
            data:{type,id,description,price},
        })
        if(data.success){
            alert(data.message)
            getItem()
        }else{
            alert(data.message)
        }
        
    }
    //get item
    const getItem=async()=>{
        try {
            const {data}=await axios({
                method: "GET",
                url:"/api/v1/get-items"
            })
            if(data.success){
                setData(data.data)
            }else{
                alert(data.message)
            }
        } catch (error) {
            console.log(error)
        }
    }
    useEffect(()=>{
        getItem()
    },[])
    const design=(
        <>
            <form onSubmit={createItem}>
                <div className="form-group">
                    <label>Item Decription</label>
                    <input required onInput={(e)=>setDescription(e.target.value)} type="text" className="form-control" placeholder="Enter Item Name" />
                </div>
                <div className="form-group">
                    <label>Item Id</label>
                    <input required onInput={(e)=>setId(e.target.value)} type="text" className="form-control" placeholder="Enter Item id" />
                </div>
                <div className="form-group">
                    <label>Item Price</label>
                    <input required onInput={(e)=>setPrice(e.target.value)} type="number" className="form-control" placeholder="Enter Item Price" />
                </div>
                <div className="form-group">
                    <label>Item Type</label>
                    <select className="form-control" onChange={(e)=>setType(e.target.value)}>
                        <option value={"2"}>perishable</option>
                        <option value={"1"}>non-perishable</option>
                    </select>
                </div>
                {/* <div className="form-group">
                    <input type="file" accept="image/*" className="form-control" name="photo" onChange={(e)=>setPhoto(e.target.files[0])}/>
                </div> */}
                <button type="submit" className="btn btn-primary">Submit</button>
            </form>
            <h3 className="text-center">All Items</h3>
            <table className="table table-striped mt-4">
                <thead>
                    <tr>
                        <th scope="col">Description</th>
                        <th scope="col">Id</th>
                        <th scope="col">Type</th>
                    </tr>
                </thead>
                <tbody>
                 
                    {
                        data.length===0?"":data?.map((item,index)=>{
                            return(
                                <tr key={index}>
                                    <td>{item.description}</td>
                                    <td>{item.id}</td>
                                    <td>{item.type==="1"?"perishable":"not-perishable"}</td>
                                </tr>
                            )
                           })
                    }
                </tbody>
            </table>
        </>
    )
    return design;
}

export default CreateItem;