import { useEffect, useState } from "react"

import axios from "axios"
import { Link, } from "react-router-dom"
import { Button } from "@mui/material"
import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';



const Home = () => {
    const [itemData, setItemData] = useState({})
    const [data, setData] = useState([])
    const [zone, setZone] = useState(5)
    const [totalPrice, setTotalPrice] = useState(0)
    const [basePrice, setBasePrice] = useState(10)
    const [perKmPirce, setPerKmPirce] = useState(0)


    function MediaCard({ data }) {
        return (
            <Card sx={{ maxWidth: 345 }} className='m-3'>

                <CardContent>
                    <Typography gutterBottom variant="h6" component="div">
                        description:
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                        {data.description}
                    </Typography>
                    <Typography gutterBottom component="div" variant='body2' className='mt-2'>
                        Type:
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                        {data.type === "1" ? "perishable" : "non perishable"}
                    </Typography>
                    <Typography gutterBottom component="div" variant='body2' className='mt-2'>
                        Price:
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                        {data.price}
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button size="small" variant='contained' onClick={() => setItemData(data)}>Select Item</Button>
                </CardActions>
            </Card>
        );
    }

    const getItem = async () => {
        const { data } = await axios({
            method: "GET",
            url: "/api/v1/get-items",
        })
        if (data.success) {
            setData(data.data)
        } else {
            console.log(data.message)
        }
    }

    const chechkPrice = (itemData) => {
        let itemPrice;

        if (itemData.price == undefined) {
            itemPrice = 0
        } else {

            itemPrice = Number(itemData.price)
        }

        let zonePrice = 0
        if (zone > 5) {
            console.log(itemData.type)
            if (itemData.type == "2") {
                zonePrice = zone - 5
                zonePrice = zonePrice * 1
            } else {
                zonePrice = zone - 5
                zonePrice = zonePrice * 1.5
            }
            zonePrice = Number(zonePrice)

            setPerKmPirce(zonePrice)
            setTotalPrice(itemPrice + zonePrice + 10)

        }
        setTotalPrice(itemPrice + zonePrice + 10)

    }


    useEffect(() => {
        getItem()
    }, [])

    const design = (
        <>
            <div className="">
                <Link to={"/admin"}>Admin Dashboard</Link>

            </div>


            <div className="row">
                <div className="col-md-6">
                    <h3 className="text-center">All Items</h3>
                    <div className="d-flex ">

                        {data?.map((item, index) => {
                            return (
                                <MediaCard key={index} data={item} />
                            )
                        })}
                    </div>
                </div>

                <div className="col-md-6 border p-3">
                    <h3 className="text-center">Check Price</h3>
                    {itemData.description ? <div>
                        <div className="d-flex">
                            <div className="mx-3">
                                <h6>description</h6>
                                <p>{itemData.description}</p>
                            </div>
                            <div className="mx-3">
                                <h6>Price</h6>
                                <p>{itemData.price}</p>
                            </div>
                            <div className="mx-3">
                                <h6>type</h6>
                                <p>{itemData.type === "1" ? " perishable" : "non perishable"}</p>
                            </div>

                        </div>
                    </div> : ""}
                    <div className="form-group">
                        <label>Select Your Zone</label>
                        <select className="form-control" onChange={(e) => {
                            setZone(e.target.value);

                        }}>
                            <option value="5">zone 1  (5km) (paliyad road) (haveli chowk) (marvadi gali) (green circle)</option>
                            <option value="10">zone 2  (10km) (high tower) (tower Road) (main market) (nanai glai)</option>
                            <option value="15">zone 3   (15km) (limbi chowck) (canara bank area) (haveli gam ) (hadadad gam)</option>
                        </select>
                    </div>
                    {
                        <div>
                            <h6>per km charge <br />(we charge only if distance is above 5 km )(perishable:1.5/km) (non perishable:1/km):{perKmPirce}</h6>
                            <h6>base price:{10}</h6>
                            <h6>item price:{itemData.price}</h6>
                            <h6>Total Pirce :{totalPrice == 10 ? "please select item" : totalPrice}</h6>
                        </div>
                    }
                    <Button variant="contained" color="success" onClick={() => chechkPrice(itemData)}>Check Delevery Price</Button>
                </div>
            </div>
        </>
    )
    return design;
}

export default Home;
