import axios from "axios"
import { useEffect, useState } from "react"

const CreateOrg =()=>{
    const [name,setName]=useState("")
    const [id,setId]=useState("")
    const [data,setData]=useState([])
    
    
    const createOrg=async(e)=>{
        e.preventDefault()
        const {data}=await axios({
            method: "POST",
            url:"/api/v1/create-organization",
            data:{id,name},
        })
        if(data.success){
            alert(data.message)
            getOrganization()
        }else{
            alert(data.message)
        }
    }

    //get organization
    const getOrganization = async()=>{
    try {
        const {data}=await axios({
            method: "GET",
            url:"/api/v1/get-organization",
        })
        if(data.success){
            setData(data.data)
        }else{
            alert(data.message)
        }
    } catch (error) {
        
    }
    }
    useEffect(()=>{
        getOrganization()
    },[])
    const design=(
        <>
            <form onSubmit={createOrg}>
                <div className="form-group">
                    <label>Organization Name</label>
                    <input required onInput={(e)=>setName(e.target.value)}  name="name" type="text" className="form-control"/>
                </div>
                <div className="form-group">
                    <label>Organization Id</label>
                    <input required onInput={(e)=>setId(e.target.value)} name="id" type="text" className="form-control"/>
                </div>
                
                <button type="submit" className="btn btn-primary">Submit</button>
            </form>
            <h3 className="text-center mt-4">All Organization List</h3>
            <table className="table table-striped">
                <thead>
                    <tr>
                        <th>organization name</th>
                        <th>organization id</th>
                    </tr>
                </thead>
                <tbody>
                    {data?.map((item,index)=>{
                        return(
                            <tr key={index}>
                                <td>{item.name}</td>
                                <td>{item.id}</td>
                            </tr>
                        )
                    })}
                </tbody>
            </table>
        </>
    )
    return design;
}
export default CreateOrg;