import { Outlet,Link } from "react-router-dom";


const Admin =()=>{
    const design=(
        <>
            <h1 className="text-center">Admin Dashboard</h1>
            <div className="row">
                <div className="col-md-3 list-group">
                    <ul>
                        <Link to={"/admin/create-org"} className="nav-link list-group-item list-group-item-action">Create Organizatino</Link>
                        <Link to={"/admin/create-item"} className="nav-link list-group-item list-group-item-action">Create Item</Link>
                        <Link to={"/"} className="nav-link list-group-item list-group-item-action">Home</Link>
                    </ul>
                </div>
                <div className="col-md-6">
                    <Outlet/>
                </div>
                <div className="col-md-3"></div>
            </div>
        </>
    )
    return design;
}

export default Admin;